.generation {
    padding: 120px 0;
    background-color: #f7f7f7;

    &__block {}

    @media (max-width: 900px) {
        padding: 80px 0;
    }

    @media (max-width: 600px) {
        padding: 30px 0;
    }
}

//==============================================================================================================================================================================================
.block {
    text-align: center;

    &__icon {
        margin: 0 0 30px 0;
    }


    &__title {
        color: #353738;
        font-size: 18px;
        font-weight: bold;
        line-height: 24px;
        letter-spacing: 3.6px;
        margin: 0 0 18px 0;
        text-transform: uppercase;
    }


    &__text {
        color: #848789;
        font-size: 18px;
        line-height: 34px;
        // .block__text_j

        &_j {
            text-align: justify;
            font-size: 16px;
            line-height: 30px;

            &:after {
                content: '';
                display: block;
                margin: 35px auto 0 auto;
                width: 50px;
                height: 1px;
                background-color: #ebebeb;
            }
        }
    }

    @media (max-width: 1500px) {
        &__text {
            &_j {
                &:after {
                    margin: 15px auto 0 auto;
                }
            }
        }
    }
}