.wrapper {
    overflow: hidden;
    min-height: 100%;
}

.page {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    display: flex;
    font-size: 0;
    flex-direction: column;
}

.header {
    position: absolute;
    width: 100%;
    z-index: 50;
    left: 0;
    top: 35px;

    &__row {
        display: flex;
        font-size: 0;
        justify-content: center;
        align-items: center;
        height: 85px;
    }

    // .header__menu

    &__menu {}

    // .header__logo

    &__logo {
        position: absolute;
        left: 50%;
        top: 0;
        z-index: 2;
        transform: translate3d(-50%, 0px, 0px);
    }

    @media (max-width: 767px) {
        top: 15px;
    }
}

.menu {
    &__list {
        display: flex;
        font-size: 0;

        li {
            position: relative;
            padding: 0 37.5px;

            &:nth-child(2) {
                padding: 0 110px 0 37.5px;
            }

            &:nth-child(3) {
                padding: 0 37.5px 0 110px;
            }

            &:after {
                content: '';
                width: 2px;
                height: 2px;
                background-color: #4d4959;
                border-radius: 50%;
                position: absolute;
                right: 0;
                top: 50%;
                margin: -1.5px 0 0 0;
            }

            &:nth-child(2),
            &:last-child {
                &:after {
                    display: none;
                }
            }
        }
    }

    // .menu__link

    &__link {
        color: #4d4959;
        font-size: 13px;
        font-weight: 700;
        text-transform: uppercase;
        letter-spacing: 2.6px;
    }

    @media (max-width: 767px) {
        &__body {
            position: fixed;
            width: 100%;
            height: 100%;
            top: 0;
            left: 0;
            background-color: #4d4959;
            padding: 150px 0 24px 0;
            transform: translate3d(0px, -100%, 0px);
            transition: all 0.3s ease 0s;
            &.active {
                transform: translate3d(0px, 0px, 0px);
            }
        }

        &__list {
            flex-direction: column;
            justify-content: center;
            align-items: center;

            li {
                padding: 0 0 45px 0;

                &:nth-child(2),
                &:nth-child(3) {
                    padding: 0 0 35px 0;

                    &:after {
                        display: block;
                    }
                }

                &:last-child {
                    padding: 0;
                }

                &:after {
                    width: 4px;
                    height: 4px;
                    background-color: #fff;
                    right: 50%;
                    top: 40px;
                    margin: 0 -2px 0 0;
                }
            }
        }

        &__link {
            color: #fff;
            font-size: 20px;
        }
    }
}

.icon-menu {
    @media (max-width: 767px) {
        display: block;
        position: absolute;
        top: 33px;
        right: 7%;
        width: 30px;
        height: 18px;
        cursor: pointer;
        z-index: 5;

        span {
            transition: all 0.3s ease 0s;
            top: 8px;
            left: 0px;
            position: absolute;
            width: 100%;
            height: 2px;
            background-color: #4d4959;

            &:first-child {
                top: 0px;
            }

            &:last-child {
                top: auto;
                bottom: 0px;
            }
        }

        &.active {
            span {
                transform: scale(0);
                background-color: #fff;

                &:first-child {
                    transform: rotate(-45deg);
                    top: 8px;
                }

                &:last-child {
                    transform: rotate(45deg);
                    bottom: 8px
                }
            }
        }
    }
}



.main-screen {
    flex: 1 0 100%;
    position: relative;

    &__bg {
        position: absolute;
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
    }
}

.content {
    font-size: 20px;
}